.subscription {
  padding-left: 38px;
  padding-top: 10px;
  width: 100%;
  .subscription__title {
    font-size: 16px;
    line-height: 24px;
  }
  .subscription__cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 41px;
    width: 100%;
    .subscription__card {
      background: white;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11), 0 2px 1px rgba(0, 0, 0, 0.09), 0 1px 3px rgba(0, 0, 0, 0.16);
      border-radius: 4px;
      padding: 24px 24px 43px;
      position: relative;
      margin-bottom: 34px;
      .subscription__top {
        display: flex;
        align-items: center;
        .subscription__image {
          margin-right: 12px;
        }
        .subscription__card-title {
          font-size: 16px;
          line-height: 24px;
          color: $darkGrey;
        }
      }
      .subscription__right-top {
        position: absolute;
        width: 110px;
        right: 10px;
        top: -10px;
        .subscription__popular {
          font-weight: 500;
          font-size: 12px;
          line-height: 18px;
          color: white;
          background: $lightGreen;
          padding: 2px 27px;
          text-align: center;
          border-radius: 18px 0 18px 0;
        }
        .subscription__current {
          font-weight: 300;
          font-size: 13px;
          line-height: 21px;
          color: $darkGrey;
          display: flex;
          align-items: center;
          padding: 2px 12px;
          background: $lowOpacityGreen;
          border: .5px solid $darkGrey;
          border-radius: 16px;
          justify-content: center;
          &:before {
            content: "";
            width: 18px;
            height: 18px;
            padding-right: 8px;
            // background: url("/images/profile/subscriptions/tick.svg") no-repeat;
          }
        }
        .subscription__price {
          font-weight: 700;
          font-size: 30px;
          line-height: 20px;
          text-align: center;
          padding-top: 10px;
        }
      }
      .subscription__advantages {
        padding-top: 16px;
        max-width: 335px;
        height: 60px;
        .subscription__advantage {
          font-weight: 300;
          font-size: 12px;
          line-height: 18px;
          color: $darkGrey;
          background: $lowOpacityGreen;
          border-radius: 16px;
          border: 1px solid $green;
          padding: 0 12px;
          margin-right: 4px;
          display: inline-block;
          margin-bottom: 10px;
        }
      }
      .subscription__description {
        font-weight: 300;
        font-size: 13px;
        line-height: 21px;
        padding-top: 18px;
        max-width: 335px;
      }
      .subscription__btns {
        text-align: center;
        padding-top: 10px;
        position: absolute;
        left: 0;
        bottom: 10px;
        width: 100%;
        .subscription__btn {
          font-weight: 700;
          font-size: 14px;
          line-height: 20px;
          color: white;
          background: $green;
          padding: 4px 24px;
          border-radius: 18px;
          cursor: pointer;
          transition: all .25s ease;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
            }
          }
          &.down {
            background: $lightGrey;
          }
        }
      }
    }
  }
}


@media (max-width: 1000px) {
  .subscription {
    padding-left: 0;
    padding-top: 30px;
  }
}


@media (max-width: 500px) {
  .subscription {
    .subscription__cards {
      .subscription__card {
        width: 100%;
        .subscription__top {
          display: flex;
          align-items: center;
          .subscription__image {
            margin-right: 12px;
            width: 32px!important;
            height: 32px!important;
          }
          .subscription__card-title {
            font-size: 12px;
            line-height: 24px;
          }
        }
        .subscription__right-top {
          width: 80px;
          .subscription__popular {
            font-weight: 500;
            font-size: 11px;
            line-height: 18px;
            padding: 2px 18px;
          }
          .subscription__current {
            font-size: 11px;
            line-height: 21px;
            padding: 2px 8px;
          }
          .subscription__price {
            font-size: 20px;
            line-height: 20px;
          }
        }
        .subscription__advantages {
          max-width: none;
          .subscription__advantage {
            font-size: 10px;
            line-height: 18px;
            padding: 2px 6px;
          }
        }
        .subscription__description {
          font-size: 10px;
          line-height: 21px;
          max-width: none;
        }
      }
    }
  }

}