.transcripts {
  padding-top: 35px;
  @include default-width;
  .transcripts__content {
    width: 100%;
    max-width: 1100px;
    display: flex;
    .transcripts__tabs {
      width: 25%;
      .transcripts__tab {
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        .transcripts__tab-top {
          font-weight: 700;
          font-size: 13px;
          line-height: 22px;
          color: $darkGrey;
          padding: 11px 16px;
          background: $greyF5;
          position: relative;
          border-bottom: 1px solid $footerGrey;
          cursor: pointer;
          &:after {
            content: "";
            position: absolute;
            right: 16px;
            top: 50%;
            border: solid black;
            border-width: 0 3px 3px 0;
            display: inline-block;
            padding: 3px;
            transition: all .25s ease;
            transform: translateY(-50%) rotate(45deg);
          }
        }
        .transcripts__dropdown {
          max-height: 0;
          transition: all .25s ease;
          ul {
            padding: 7px 16px;
            li {
              div {
                display: flex;
                justify-content: space-between;
                cursor: pointer;
                span {
                  font-weight: 300;
                  font-size: 11px;
                  line-height: 22px;
                  color: $lightGrey;
                }
              }
              &.active {
                div {
                  span {
                    font-weight: 700;
                    color: $darkGrey;
                  }
                }
              }
            }
          }
        }
        &.show {
          .transcripts__tab-top {
            &:after {
              transform: translateY(-50%) rotate(-135deg);
            }
          }
          .transcripts__dropdown {
            max-height: 1000px;
          }
        }
      }
    }
    .transcripts__chat {
      width: 75%;
      border: 1px solid $tabsGrey;
      padding: 26px 16px 42px;
      box-sizing: border-box;
      .transcripts__message {
        padding: 12px 21px;
        border: 1px solid $tabsGrey;
        border-radius: 20px;
        color: $darkGrey;
        font-size: 13px;
        line-height: 22px;
        margin-top: 8px;
        &:first-child {
          margin-top: 0;
        }
        .transcripts__name {
          font-weight: 700;
        }
        .transcripts__text {
          font-weight: 400;
          padding-top: 25px;
        }
      }
    }
  }
}