.article-preview{
    width: 252px;
    margin: 0 15px;
    margin-bottom: 30px;
    border: 1px solid #8A879D;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    cursor: pointer;
    .image{
        width: 250px;
        height: 150px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    h4{
        padding: 10px;
        font-size: 18px;
        line-height: 1.3;
    }
    .bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #65617B;
        width: 100%;
        padding: 10px;
        .date{
            font-size: 11px;
        }
        .post-categories{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .category{
                padding: 4px 7px;
                margin-left: 10px;
                border: 1px solid #13714C;
                background: #3ab67d0d;
                border-radius: 16px;
                font-size: 10px;
                transition: 0.1s;
                &:first-child{
                    margin-left: 0;
                }
                &:hover{
                    background: #3ab67c28;
                }
                &:active{
                    background: #3ab67c42;
                }
            }
        }
    }
    @media (max-width: 625px) {
        margin: 15px 0;
    }
}