@mixin btns-box {
  display: inline-flex;
  align-items: center;
  justify-content: flex-end;
  padding: 12px 10px;
  border-radius: 18px;
  >div, >a {
    font-weight: 700;
    font-size: 11px;
    line-height: 18px;
    color: rgba(101, 97, 123, 0.29);
    border: 1px solid $tabsGrey;
    background: white;
    padding: 5px 10px;
    transition: all .25s ease;
    user-select: none;
    cursor: pointer;
    &:first-child {
      border-radius: 18px 0 0 18px;
    }

    &:last-child {
      border-radius: 0 18px 18px 0;
    }

    &.active {
      background: $tableGrey;
      color: $darkGrey;
      border: 1px solid $darkGrey;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: $tableGrey;
          color: $darkGrey;
          border: 1px solid $darkGrey;
        }
      }
    }
    @media (hover: hover) and (pointer: fine) {
      &:hover {
        color: rgba(101, 97, 123, 0.7);
        background: rgba(233, 232, 240, 0.1);
        border: 1px solid rgba(0, 0, 0, 0.29);
      }
    }
    &:focus {
      background: $tableGrey;
      color: $darkGrey;
      border: 1px solid $darkGrey;
    }
  }
  @media (max-width: 700px) {
    padding: 5px 7px;
    >div, >a {
      font-size: 9px;
      line-height: 18px;
      padding: 3px 9px;
    }
  }
}


@mixin footer-head {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      color: $lightGreen;
    }
  }
  &:focus {
    color: $veryLightGreen;
  }
}

@mixin settings-head {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      background: $tableGrey;
    }
  }
  &:focus {
    background: $tabsGrey;
  }
}