.management {
  padding-top: 38px;
  .biography, .compensation {
    .biography__content, .compensation__content {
      @include default-width;
      .biography__title, .compensation__title {
        font-size: 20px;
        line-height: 18px;
        color: $darkGrey;
      }
      table {
        margin-top: 18px;
        width: 100%;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        tr {
          border-bottom: 1px solid $footerGrey;
          th, td {
            text-align: center;
            color: $darkGrey;
            padding: 7px 16px;
            vertical-align: middle;
            border-right: 1px solid $footerGrey;
            &:last-child {
              border-right: none;
            }
          }
        }
        thead {
          tr {
            background: $tableGrey;
            th {
              font-weight: 700;
              font-size: 12px;
              line-height: 22px;
            }
          }
        }
        tbody {
          tr {
            td {
              font-weight: 300;
              font-size: 11px;
              line-height: 22px;
              p {
                padding-top: 10px;
                &:first-child {
                  padding-top: 0;
                }
              }
            }
          }
        }
      }
    }
  }
  .biography {
    .biography__content {
      table {
        max-width: 890px;
        tbody {
          tr {
            td {
              padding: 7px 26px;
            }
            td:last-child {
              text-align: left;
              padding: 7px 16px;
              a {
                text-decoration: underline!important;
                color: $lightGreen;
              }
            }
          }
        }
      }
    }
  }
  .compensation {
    padding-top: 82px;
    .compensation__content {
      table {
        max-width: 640px;
      }
    }
  }
}


@media (max-width: 700px) {
  .management {
    padding-top: 11px;
    .biography, .compensation {
      .biography__content, .compensation__content {
        width: 100%;
        padding: 0 20px;
        .biography__title, .compensation__title {
          font-size: 12px;
          line-height: 18px;
        }
        table {
          thead {
            tr {
              th {
                font-size: 10px;
              }
            }
          }
          tbody {
            tr {
              td {
                font-size: 9px;
                line-height: 15px;
              }
            }
          }
        }
      }
    }
    .biography {
      .biography__content {
        table {
          tbody {
            tr {
              td {
                padding: 7px 12px;
              }
              td:last-child {
                padding: 7px 12px;
              }
            }
          }
        }
      }
    }
    .compensation {
      padding-top: 25px;
      .compensation__content {
        padding: 0 20px;
        .compensation__table {
          overflow-x: auto;
          table {
            tbody {
              tr {
                td {
                  p {
                    white-space: nowrap;
                  }
                }
              }
            }
          }
        }

      }
    }
  }
}