@keyframes spiner{
    0%{
        transform: rotate(0deg);
        border: 5px solid rgb(54, 52, 52);
        border-left: 5px solid rgb(190, 181, 181);
    }
    50%{
        transform: rotate(180deg);
        border: 5px solid rgb(190, 181, 181);
        border-left: 5px solid rgb(54, 52, 52);
    }
    100%{
        transform: rotate(360deg);
        border: 5px solid rgb(54, 52, 52);
        border-left: 5px solid rgb(190, 181, 181);
    }
}

.spiner{
    width: 100vw;
    height: 100vh;
    &.content-waiting{
        margin-top: 100px;
        height: 70px;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .circle{
        width: 70px;
        height: 70px;
        border: 5px solid rgb(54, 52, 52);
        border-left: 5px solid rgb(190, 181, 181);
        border-radius: 100%;
        transform: rotate(0deg);
        animation:  1s linear infinite spiner;
    }
    p{
        margin-top: 30px;
        padding: 0 5px;
        text-align: center;
        line-height: 1.2;
    }
}