.date-sorting__content {
  display: flex;
  justify-content: space-between;
  padding: 25px 6px 10px;
  .date-sorting__text {
    font-size: 12px;
    line-height: 14px;
    color: black;
  }
  .date-sorting__select {
    width: 130px;
    outline: none;
    border: none;
    background: white;
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14), 0 3px 14px rgba(0, 0, 0, 0.12), 0 5px 5px rgba(0, 0, 0, 0.2);
  }
}