footer {
  margin-top: 200px;
  background: white;
  border-top: 1px solid $footerGrey;
  nav {
    padding: 22px 0;
    ul {
      display: flex;
      justify-content: center;
      width: 100%;
      margin: 0 auto;
      li {
        padding-right: 32px;
        a {
          font-family: 'Roboto', sans-serif;
          font-weight: 300;
          font-size: 14px;
          line-height: 21px;
          color: $darkGrey;
          @include footer-head;
        }
        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}


@media (max-width: 750px) {
  footer {
    margin-top: 100px;
    nav {
      padding: 22px 34px;
      ul {
        li {
          a {
            font-size: 12px;
            line-height: 21px;
          }
        }
      }
    }
  }
}

@media (max-width: 500px) {
  footer {
    nav {
      padding: 22px 16px;
      ul {
        justify-content: space-between;
        li {
          padding: 0;
          a {
            font-size: 12px;
            line-height: 21px;
          }
        }
      }
    }
  }
}