.currency-trigger {
  span {
    padding-right: 2px;
  }
}


.currency__pop-up {
  width: 100px;
  height: 150px;
  padding-top: 10px;
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14), 0 3px 14px rgba(0, 0, 0, 0.12), 0 5px 5px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background: white;
  .currency__option {
    padding: 2px 0 2px 5px;
    font-weight: 300;
    font-size: 10px;
    line-height: 21px;
    color: $darkGrey;
    border: 1px solid transparent;
    cursor: pointer;
    &.active {
      border-color: $lightGreen;
      background: $lowOpacityGreen;
      box-shadow: 0 4px 4px $shadow;
    }
    @media (hover: hover) and (pointer: fine) {
      &:hover {
        border-color: $lightGreen;
        background: $lowOpacityGreen;
        box-shadow: 0 4px 4px $shadow;
      }
    }
  }
}