.company-info {
  border: 1px solid $tabsGrey;
  margin-bottom: 20px;
  .table-nav {
    background: none;
    box-shadow: none;
    padding: 5px 10px 5px 20px;
  }
  .company-info__content {
    padding: 0 10px 0 20px;
    .company-info__stats {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding-top: 20px;
      padding-right: 20px;
      .company-info__stats-box {
        padding-bottom: 12px;
        .company-info__stat-row {
          padding-top: 4px;
          &:first-child {
            padding-top: 0;
          }
          span {
            font-size: 12px;
            line-height: 16px;
            color: $shadow;
            padding-right: 2px;
            &:last-child {
              color: $darkGrey;
              padding-right: 0;
            }
          }
        }
      }
    }
    .company-info__text {
      padding-top: 30px;
      font-size: 12px;
      line-height: 21px;
      color: $darkGrey;
    }
  }
}

@media (max-width: 700px) {
  .company-info {
    border-radius: 8px;
  }
}