.language-pop-up__box {
  position: relative;
  @media (max-width: 700px) {
    position: static;
  }
}

.language-pop-up {
  opacity: 0;
  transform: scale(.8);
  pointer-events: none;
  top: 39px;
  right: 0;
  position: absolute;
  width: 266px;
  max-height: 240px;
  overflow-y: auto;
  background: white;
  border: 1px solid $tabsGrey;
  padding: 18px 23px;
  transition: all .25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  @media (max-width: 700px) {
    right: 0;
    left: 0;
    margin: 0 auto;
    top: 100%;
  }
  .language-pop-up__btn {
    display: flex;
    align-items: center;
    border: none;
    outline: none;
    background: none;
    margin-top: 10px;
    cursor: pointer;
    &:first-child {
      margin-top: 0;
    }
    img {

    }
    span {
      font-size: 12px;
      line-height: 18px;
      color: black;
      padding-left: 8px;
    }
    @media (hover: hover) and (pointer: fine) {
      &:hover {
        span {
          text-decoration: underline;
        }
      }
    }
    &.active {
      span {
        color: $lightGreen;
        text-decoration: underline;
      }
    }
  }
  &.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: auto;
  }
}