

.header__profile-box {
  position: relative;
}

.profile-popup {
  opacity: 0;
  transform: scale(.8);
  pointer-events: none;
  position: absolute;
  bottom: -128px;
  right: 0;
  background: white;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.11), 0 1px 10px rgba(0, 0, 0, 0.09), 0 2px 4px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  transition: all .25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  .profile__top {
    padding: 6px 16px;
    border-bottom: 1px solid $tabsGrey;
    display: flex;
    align-items: center;
    cursor: pointer;
    .profile__icon {
      width: 48px;
      height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 300;
      font-size: 24px;
      line-height: 24px;
      color: white;
      background: $green;
      border-radius: 50%;
    }
    .profile__info {
      padding-left: 10px;
      .profile__username {
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        color: $darkGrey;
      }
      .profile__email {
        font-size: 12px;
        line-height: 18px;
        color: $darkGrey;
        padding-top: 2px;
      }
    }
  }
  .profile__btn {
    text-align: center;
    padding: 10px 16px;
    .profile__logout {
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      background: $green;
      border-radius: 18px;
      color: white;
      padding: 8px 80px;
      display: flex;
      align-items: center;
      cursor: pointer;
      &:before {
        content: "";
        width: 18px;
        height: 18px;
        // background: url("/images/header/logout.svg");
        margin-right: 8px;
      }
    }
  }
  &.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: auto;
  }
}