.competitors {
  padding-top: 35px;
  @include default-width;
  .competitors__content {
    width: 100%;
    max-width: 605px;
    .competitors__top {
      display: flex;
      justify-content: space-between;
      .competitors__title {
        font-size: 20px;
        line-height: 18px;
        color: $darkGrey;
      }
      .competitors__top-right {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .competitors__add {
          padding: 2px 8px 2px 26px;
          border: 1px solid $lightGrey;
          border-radius: 18px;
          color: $darkGrey;
          position: relative;
          cursor: pointer;
          user-select: none;
          margin-right: 10px;
          &:before {
            content: "";
            position: absolute;
            width: 10px;
            height: 10px;
            // background: url("/images/competitors/plus.svg");
            top: 50%;
            left: 8px;
            transform: translate(0 , -50%);
          }
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              box-shadow: 0 4px 10px $shadow;
            }
          }
          &:focus {
            background: $tableGrey;
            box-shadow: 0 4px 10px $shadow;
          }
        }
        .competitors__question {
          border-radius: 50%;
          @include settings-head;
          user-select: none;
        }
      }
    }
    table {
      width: 100%;
      margin-top: 12px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      tr {
        border-bottom: 1px solid $footerGrey;
        th, td {
          text-align: center;
          color: $darkGrey;
          padding: 7px 16px;
          vertical-align: middle;
          border-right: 1px solid $footerGrey;
          &:last-child {
            border-right: none;
          }
        }
        &:last-child {
          border-bottom: 0;
        }
      }
      thead {
        tr {
          background: $tableGrey;
          th {
            font-weight: 700;
            font-size: 12px;
            line-height: 22px;
          }
        }
      }
      tbody {
        tr {
          td {
            font-weight: 300;
            font-size: 11px;
            line-height: 22px;
            &:first-child {
              text-align: left;
            }
          }
        }
      }
    }
  }
}


@media (max-width: 600px) {
  .competitors {
    width: 100%;
    padding: 35px 20px 0;
    .competitors__content {
      table {
        tr {
          th, td {
            padding: 7px 0;
          }
        }
        thead {
          tr {
            th {
              font-size: 10px;
            }
          }
        }
        tbody {
          tr {
            td {
              font-size: 9px;
              &:first-child {
                padding-left: 5px;
              }
            }
          }
        }
      }
    }
  }

}