.personal {
  padding-top: 60px;
  padding-left: 63px;
  width: 100%;
  max-width: 680px;
  .personal__title {
    font-size: 16px;
    line-height: 24px;
  }
  form {
    padding-top: 11px;
    width: 100%;
    fieldset {
      .personal__field {
        position: relative;
        margin-top: 30px;
        overflow: hidden;
        &:first-child {
          margin-top: 0;
        }
        .personal__placeholder {
          font-size: 16px;
          line-height: 24px;
          color: rgba(0, 0, 0, 0.54);
          position: absolute;
          top: 28px;
          left: 16px;
          transition: all .25s ease;
          cursor: text;
        }
        input, select {
          width: 100%;
          font-size: 16px;
          line-height: 24px;
          background: rgba(138, 135, 157, 0.04);
          border-radius: 4px 4px 0 0;
          outline: none;
          border-bottom: 1px solid $lightGrey;
          border-left: none;
          border-right: none;
          border-top: none;
          padding: 28px 16px 4px;
          option {
            background: white;
            font-size: 14px;
            line-height: 18px;
            color: $lightGrey;
          }
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: rgba(138, 135, 157, 0.1);
            }
          }
          &:focus {
            background: rgba(138, 135, 157, 0.2);
            border-color: $lightGreen;
          }
          &:focus ~ .personal__placeholder {
            top: 8px;
            font-size: 12px;
            color: $lightGreen;
            line-height: 16px;
          }
          &:not(:placeholder-shown) ~ .personal__placeholder {
            top: 8px;
            font-size: 12px;
            line-height: 16px;
          }
        }

        .personal__eye {
          position: absolute;
          top: 24px;
          right: 16px;
          cursor: pointer;
          &.closed {
            display: none;
          }
        }
        p {
          font-size: 12px;
          line-height: 16px;
          color: rgba(0, 0, 0, 0.6);
          padding-top: 4px;
          padding-left: 16px;
        }
      }
    }
    .personal__submit {
      display: block;
      text-align: center;
      padding-top: 30px;
      input {
        font-weight: 700;
        font-size: 14px;
        line-height: 20px;
        padding: 8px 100px;
        background: $lightGreen;
        color: white;
        border-radius: 18px;
        border: none;
        outline: none;
        cursor: pointer;
        transition: all .25s ease;
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
          }
        }
      }
    }
    .personal__edit {
      display: none;
      text-align: center;
      padding-top: 30px;
      .personal__edit-btn {
        display: inline-block;
        font-weight: 700;
        font-size: 14px;
        line-height: 20px;
        padding: 8px 100px;
        background: $tabsGrey;
        color: $darkGrey;
        border-radius: 18px;
        border: none;
        outline: none;
        cursor: pointer;
        transition: all .25s ease;
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
          }
        }
      }
    }
  }
  &.edit {
    form {
      fieldset {
        .personal__field {
          pointer-events: none;
          //.personal__placeholder {
          //  pointer-events: none;
          //}
          //input {
          //  pointer-events: none;
          //}
          //p {
          //  pointer-events: none;
          //}
        }
      }
      .personal__submit {
        display: none;
      }
      .personal__edit {
        display: block;
      }
    }
  }
}


@media (max-width: 1000px) {
  .personal {
    padding-top: 16px;
    padding-left: 0;
  }
}