.settings {
  display: flex;
  align-items: center;
  padding: 5px 0;
  justify-content: flex-end;
  user-select: none;
  >div {
    margin-right: 3px;
    position: relative;
    border-radius: 50%;
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    cursor: pointer;
    @include settings-head;
  }
}