.table-nav {
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  justify-content: space-between;
  align-items: center;
  background: $tableGrey;
  box-shadow: 0 1px 1px $shadow;
  padding: 5px 12px;
  .table-nav__text {
    .table-nav__title {
      font-weight: 700;
      font-size: 11px;
      line-height: 16px;
      color: $darkGrey;
    }
    .table-nav__subtitle {
      font-weight: 300;
      font-size: 10px;
      line-height: 16px;
      color: $darkGrey;
    }
  }
  .table-nav__btns {
    @include btns-box;
    background: transparent;
    >a {
      line-height: 0;
      background: white;
      cursor: pointer;
    }
  }
}


.small-table {
  width: 100%;
  margin-bottom: 20px;
  table {
    width: 100%;
  }
  tr {
    box-shadow: 0 0 1px $shadow;
    border-bottom: 1px solid $shadow;
    width: 100%;
    th, td {
      font-size: 11px;
      line-height: 16px;
      color: $darkGrey;
      padding: 7px 18px;
      text-align: center;
      vertical-align: middle;
      &:first-child {
        display: block;
        margin: 7px 0;
        padding: 0 15px;
        text-align: left;
        background: white;
        position: sticky;
        left: 0;
      }
    }
  }
  thead {
    tr {
      th:first-child {
        text-align: left;
        padding: 7px 15px 7px 7px;
      }
    }
  }
  tbody {
    tr {
      &:nth-child(odd) {
        background: $tableGrey;
        td:first-child {
          background: $tableGrey;
        }
      }
    }
  }
  &.tight {
    tr {
      th, td {
        padding: 7px;
        &:first-child {
        }
      }
    }
  }
  &.wide {
    .table-nav {
      .table-nav__btns {
        display: none;
      }
    }
  }
}







@media (max-width: 1150px) {
  .table-nav {
    .table-nav__btns {
      display: none;
    }
  }
}

@media (max-width: 700px) {
  .table-nav {
    display: flex;
    position: sticky;
    top: 0;
    left: 0;
    background: none;
    box-shadow: none;
    padding: 9px 10px 0;
    .table-nav__text {
      .table-nav__subtitle {
        font-size: 9px;
        line-height: 16px;
        color: $lightGrey;
      }
    }
    .table-nav__btns {
      display: none;
    }
  }


  .small-table {
    padding: 11px 0;
    margin-top: 13px;
    border: 1px solid $tableGrey;
    border-radius: 8px;
    tr {
      box-shadow: none;
      border-bottom: none;
      th, td {
        color: black;
        padding: 0;
        &:first-child {
          padding: 0 0 0 10px;
        }
      }
    }
    tbody {
      tr {
        &:nth-child(odd) {
          background: none;
          td:first-child {
            background: white;
          }
        }
      }
    }
  }

}

