.about-us__text p{
    margin-bottom: 30px;
}

.article_page{
    h1{
        width: 100%;
        font-size: 30px;
        margin-bottom: 15px;
        @media (max-width: 825px) {
            font-size: 18px;
        }
    }
    .date{
        font-size: 12px;
        margin-bottom: 30px;
        width: 100%;
        @media (max-width: 825px) {
            font-size: 10px;
        }
    }
    h2{
        font-size: 25px;
        margin-bottom: 30px;
        @media (max-width: 825px) {
            font-size: 18px;
        }
    }
    h3{
        font-size: 22px;
        margin-bottom: 30px;
        @media (max-width: 825px) {
            font-size: 15px;
        }
    }
    .article{
        @media (max-width: 825px) {
            border-bottom: 1px solid #65617B;
        }
        .content{
            color: #65617B;
            width: calc(100% - 220px);
            margin: 0 110px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-direction: column;
            @media (max-width: 1625px) {
                width: calc(100% - 160px);
            }
            @media (max-width: 1225px) {
                width: calc(100% - 100px);
            }
            @media (max-width: 825px) {
                width: calc(100% - 40px);
            }
            .saboxplugin-wrap{
                width: 100%;
                margin-bottom: 30px;
                .saboxplugin-tab{
                    img{
                        width: 50px;
                        margin-bottom: 10px;
                        height: 50px;
                    }
                    .saboxplugin-authorname{
                        margin-bottom: 5px;
                    }
                }
            }
            .message-block{
                padding: 10px 25px;
                border: 1px solid #22ab94;
                border-left: 4px solid #22ab94;
                @media (max-width: 825px) {
                    margin-bottom: 30px;
                }
                p{
                    margin-bottom: 0;
                    color: black;
                    b{
                        font-weight: 700;
                    }
                }
            }
            .wp-content{
                width: 100%;
            }
        }
        h2{
            width: 100%;
            line-height: 1.1;
        }
        p{
            line-height: 1.7;
            font-size: 14px;
            margin-bottom: 30px;
            width: 100%;
            @media (max-width: 825px) {
                font-size: 12px;
            }
        }
        ul{
            margin-bottom: 30px;
            width: 100%;
            font-size: 14px;
            li{
                padding: 5px 0;
            }
        }
        figure{
            width: 80%;
            margin-bottom: 30px;
        }
        img{
            width: 100%;
            height: 100%;
        }
    }
    .popular-articles{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        @media (max-width: 825px) {
            margin-top: 30px;
        }
    }
}

.faq_content .saboxplugin-wrap{
    display: none;
}