.tabs {
  padding-top: 57px;
  @include default-width;
  ul {
    border-bottom: 1px solid $tabsGrey;
    display: flex;
    flex-wrap: wrap;
    li {
      a {
        display: flex;
        align-items: center;
        padding: 8px 16px;
        border-bottom: 2px solid transparent;
        transition: all .5s ease;
        user-select: none;
        span {
          font-weight: 700;
          font-size: 12px;
          line-height: 18px;
          color: $lightGrey;
          padding-left: 4px;
          transition: all .25s ease;
        }
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            border-bottom: 2px solid $green;
          }
        }
      }
      &.active {
        a {
          border-bottom: 2px solid $green;
          position: relative;
          &:after {
            content: "";
            position: absolute;
            width: 1px;
            height: 10px;
            background: $tabsGrey;
            top: 50%;
            right: 0;
            transform: translate(0 , -50%);
          }
          span {
            color: $darkGrey;
          }
        }
      }
    }
  }
}

@media (max-width: 750px) {
  .tabs {
    padding:8px 20px 0;
    width: 100%;
    ul {
      li {
        a {
          padding: 8px;
          img {
            width: 15px!important;
            height: 15px!important;
          }
          span {
            font-size: 10px;
            line-height: 18px;
            padding: 0;
          }
        }
      }
    }
  }
}