.news {
  @include default-width;
  padding-top: 50px;
  .news__content {
    width: 100%;
    max-width: 650px;
    .news__head {
      display: flex;
      color: $darkGrey;
      .news__head-left {
        width: 110px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 2px;
        background: $greyF5;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        border-bottom: 1px solid $footerGrey;
        span {
          display: inline-block;
          font-weight: 700;
          font-size: 12px;
          line-height: 22px;
        }
      }
      .news__btns {
        width: 82%;
        background: $greyF5;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        border-bottom: 1px solid $footerGrey;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 10px 10px 50px;
        .news__sort-btn {
          font-size: 11px;
          line-height: 18px;
          padding: 2px 30px 2px 16px;
          border: 1px solid $lightGrey;
          border-radius: 18px;
          position: relative;
          cursor: pointer;
          user-select: none;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: $tabsGrey;
            }
          }
          &:focus {
            box-shadow: 0 4px 10px $shadow;
            background: $tabsGrey;
          }
          .news__sort-btn__image {
            position: absolute;
            top: 50%;
            right: 16px;
            transform: translateY(-50%);
            width: 6px;
            height: 15px;
          }
           //&:after {
           //  content: "";
           //  position: absolute;
           //  width: 6px;
           //  height: 15px;
           //  background: url("/images/news/arrow.svg");
           //  top: 50%;
           //  right: 16px;
           //  transform: translateY(-50%);
           //}
          &.up {
            &:after {
              transform: translateY(-50%) rotate(180deg);
            }
          }
        }
        .news__right-btns {
          display: flex;
          align-items: center;
          .news__open {
            font-size: 11px;
            line-height: 18px;
            padding: 2px 16px;
            border: 1px solid $lightGrey;
            border-radius: 18px;
            margin-right: 7px;
            cursor: pointer;
            user-select: none;
            @media (hover: hover) and (pointer: fine) {
              &:hover {
                background: $tabsGrey;
              }
            }
            &:focus {
              box-shadow: 0 4px 10px $shadow;
              background: $tabsGrey;
            }
          }
          .news__question {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            cursor: pointer;
            user-select: none;
            @media (hover: hover) and (pointer: fine) {
              &:hover {
                background: $tabsGrey;
              }
            }
          }
        }
      }
    }
    .news__table {
      display: flex;
      padding-top: 7px;
      .news__table-left {
        width: 110px;
        margin-right: 2px;
        .news__btn {
          width: 100%;
          padding: 5px 16px;
          box-sizing: border-box;
          background: white;
          border-bottom: 1px solid $footerGrey;
          text-align: center;
          cursor: pointer;
          position: relative;
          box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
          span {
            font-size: 11px;
            line-height: 22px;
            color: $darkGrey;
          }
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: $greyF5;
            }
          }
          &:focus {
            background: $greyF5;
            z-index: 50;
            box-shadow: 0 4px 10px $shadow;
          }
          &.active {
            background: $greyF5;
            z-index: 50;
            box-shadow: 0 4px 10px $shadow;
          }
        }
      }
      .news__links {
        width: 82%;
        position: relative;
        z-index: 100;
        .news__row {
          display: flex;
          align-items: center;
          padding: 0 16px;
          background: white;
          box-shadow: 0 12px 17px rgba(0, 0, 0, 0.11), 0 5px 22px rgba(0, 0, 0, 0.09), 0 7px 8px rgba(0, 0, 0, 0.16);
          border-radius: 4px;
          margin-bottom: 1px;
          color: $darkGrey;
          border-left: 8px solid $darkGrey;
          box-sizing: border-box;
          cursor: pointer;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              border-color:$lightGreen;
            }
          }
          .news__row__first-col {
            flex-shrink: 1;
            display: flex;
            align-items: center;
            padding-right: 13px;
            width: 25%;
            border-right: 1px solid $tabsGrey;
            @media (max-width: 750px) {
              width: 15%;
            }
            @media (max-width: 500px) {
              width: 25%;
            }
            .news__row-img {
              margin-right: 16px;
              user-select: none;
              flex-shrink: 1;
            }
            .news__date {
              flex-shrink: 10;
              font-size: 11px;
              line-height: 21px;
            }
          }
          .news__row-title {
            flex-shrink: 10;
            width: 75%;
            @media (max-width: 750px) {
              width: 85%;
            }
            @media (max-width: 500px) {
              width: 75%;
            }
            font-size: 12px;
            line-height: 21px;
            padding: 0 0 0 13px;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          .news__row-more-info {
            font-weight: 300;
            font-size: 11px;
            line-height: 21px;
            padding-left: 13px;
          }
        }
        .my-pagination {
          padding-top: 23px;
          .pagination__content {
            display: flex;
            justify-content: center;
            align-items: center;
            span {
              font-weight: 400;
              font-size: 12px;
              line-height: 20px;
              color: $lightGrey;
              padding: 2px 16px;
              border: 1px solid $tabsGrey;
              user-select: none;
              &:first-child {
                border-radius: 18px 0 0 18px;
              }
              &:last-child {
                border-radius: 0 18px 18px 0;
              }
              @media (hover: hover) and (pointer: fine) {
                &:hover {
                  background: $greyF5;
                }
              }
              &:focus {
                background: $greyF5;
                color: $darkGrey;
              }
              &.active {
                background: $greyF5;
                color: $darkGrey;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 750px) {
  .news {
    width: 100%;
    padding: 24px 10px 0;
    .news__content {
      width: 100%;
      max-width: 650px;
      .news__head {
        .news__head-left {
          width: 80px;
          span {
            display: inline-block;
            font-size: 11px;
            line-height: 22px;
          }
        }
        .news__btns {
          width: 100%;
          padding: 10px 7px 10px 33px;
          .news__sort-btn {
            font-size: 9px;
            padding: 2px 25px 2px 10px;
            &:after {
              right: 10px;
            }
            &.up {
              &:after {
                transform: translateY(-50%) rotate(180deg);
              }
            }
          }
          .news__right-btns {
            .news__open {
              font-size: 9px;
              padding: 2px 4px;
            }
          }
        }
      }
      .news__table {
        display: block;
        padding-top: 5px;
        .news__table-left {
          display: flex;
          width: auto;
          margin-right: 0;
          .news__btn {
            width: 80px;
            padding: 5px;
            background: none;
            box-shadow: none;
            margin-right: 4px;
            &:last-child {
              margin-right: 0;
            }
            span {
              font-size: 9px;
            }
          }
        }
        .news__links {
          width: 100%;
          z-index: 100;
          padding-top: 14px;
          .news__row {
            padding: 0 0 0 5px;
            background: white;
            margin-bottom: 5px;
            border-left: 5px solid $darkGrey;
            .news__row__first-col {
              display: flex;
              align-items: center;
              padding-right: 4px;
              .news__row-img {
                margin-right: 5px;
                padding: 6px 0;
                width: 20px;
                display: flex;
                align-content: center;
                img {
                  width: 20px!important;
                }
              }
              .news__date {
                font-size: 8px;
              }
            }
            .news__row-title {
              font-size: 9px;
              padding: 0 4px;
            }
            .news__row-more-info {
              font-size: 8px;
              padding-left: 4px;
            }
          }
        }
      }
    }
  }
}