.overview-main {
  padding-top: 11px;
  .overview-main__content {
    @include default-width;
    display: flex;
    .overview-main__left {
      width: 100%;
      max-width: 235px;
      border: 1px solid $tabsGrey;
      margin-right: 11px;
      th{
        white-space: nowrap;
      }
      .settings{
        *{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      .overview-main__left-content{
        padding-top: 11px;
        >div:nth-child(odd) {
          background: $tableGrey;
        }
      }
    }
    .overview-main__right {
      width: 100%;
      overflow: hidden;
      box-sizing: border-box;
      .overview-main__graph {
        width: 100%;
        filter: drop-shadow(0 4px 5px rgba(0, 0, 0, 0.11)) drop-shadow(0 1px 10px rgba(0, 0, 0, 0.09)) drop-shadow(0 2px 4px rgba(0, 0, 0, 0.16));
        border-radius: 4px;
        background: white;
        position: relative;
        .overview-main__graph-btns {
          background: transparent;
          display: flex;
          justify-content: space-between;
          padding: 6px 10px;
          div{
            @include btns-box;
          }
          >a {
            background: white;
          }
        }
        .parrentCanvas{
          padding-right: 30px;
        }
      }
      .overview-main__high-low {
        width: 100%;
        margin-top: 10px;
        overflow-x: auto;
        .overview-main__high-low__row {
          box-shadow: 0 1px 1px $shadow;
          display: flex;
          padding: 0 12px;
          .overview-main__high-low__first-cell {
            width: 25px;
          }
          li {
            font-size: 11px;
            line-height: 14px;
            color: $darkGrey;

            padding: 3px 5px;
          }
          ul {
            width: 100%;
            display: flex;
            justify-content: space-between;
            //margin-left: 12px;
            flex-flow: row nowrap;
            align-items: center;
            li {
              width: 60px;
              min-width: 60px;
              max-width: 60px;
              text-align: center;

            }
          }
          &:nth-child(even) {
            background: $tableGrey;
          }
        }
      }
    }
  }
}

.left-row {
  display: flex;
  justify-content: space-between;
  padding: 2px 10px;
  color: $darkGrey;
  align-items: center;
  box-shadow: 0 1px 1px $shadow;
  h3 {
    font-size: 11px;
    line-height: 18px;
  }
  p {
    font-size: 12px;
    line-height: 18px;
  }
}

.left-table {
  margin-top: 7px;
  width: 100%;
  tr {
    box-shadow: 0 1px 1px $shadow;
    th {
      padding: 0 9px;
      font-size: 11px;
      line-height: 18px;
      color: $darkGrey;
      border-right: 1px solid $tabsGrey;
      &:last-child {
        border-right: none;
        text-align: right;
      }
    }
  }
  tbody {
    tr {
      &:nth-child(odd){
        background: $tableGrey;
      }
    }
  }
}




.overview-second-table {
  @include default-width;
  padding-top: 30px;
}


.other-tables {
  padding-top: 20px;
  .other-tables__content {
    @include default-width;
    .other-tables__tables {
      display: flex;
      flex-wrap: wrap;
      margin-right: -21px;
      >div {
        margin-right: 2%;
      }
      >.half {
        width: 23%;
      }
      >.full {
        width: 48%;
      }
    }
  }
}

@media (max-width: 1150px) {
  .other-tables {
    .other-tables__content {
      .other-tables__tables {
        display: flex;
        flex-wrap: wrap;
        margin-right: 0;
        >div {
          margin-right: 0;
        }
        >.half {
          width: 100%;
        }
        >.full {
          width: 100%;
        }
      }
    }
  }
}


@media (max-width: 1000px) {
  .overview-main {
    padding-top: 6px;
    .overview-main__content {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      .overview-main__left {
        order: 2;
        padding: 0 20px;
        max-width: none;
        border: none;
        margin-right: 0;
        margin-top: 22px;
        .overview-main__left-content{
          padding-top: 4px;
        }
      }
      .overview-main__right {
        order: 1;
        padding: 0 20px;
        .overview-main__graph {
          width: 100%;
          canvas {
            height: 200px;
            width: 100%;
          }
        }
        .overview-main__high-low {
          overflow-x: auto;
          padding-bottom: 10px;
          tr {
            th {
              padding: 3px 22px;
              &:first-child, &:last-child {
                padding: 3px 5px;
              }
            }
          }
        }
      }
    }
  }


  .left-table {
    tr {
      th:first-child {
          width: 50%;
      }
    }
  }

  .overview-second-table {
    width: 100%;
    padding: 30px 20px 0;
  }

  .other-tables {
    padding-top: 20px;
    .other-tables__content {
      width: 100%;
      padding: 0 20px;
      flex-wrap: wrap;
      .other-tables__left {
        width: 100%;
        order: 2;
        .other-tables__bottom {
          flex-wrap: wrap;
          .other-tables__bottom-left {
            width: 49%;
          }
          .other-tables__bottom-right {
            width: 49%;
          }
        }
      }
      .other-tables__right {
        width: 100%;
        order: 1;
        .other-tables__info-block {
          margin-top: 0;
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .other-tables {
    .other-tables__content {
      .other-tables__left {
        .other-tables__bottom {
          .other-tables__bottom-left {
            width: 100%;
          }
          .other-tables__bottom-right {
            width: 100%;
          }
        }
      }
    }
  }
}