.header__searchbar-box {
  position: relative;
  margin-left: 53px;
  width: 346px;
  @media (max-width: 700px) {
    margin-left: 8px;
  }
  @media (max-width: 620px) {
    width: 200px;
  }
  @media (max-width: 420px) {
    width: auto;
  }
  .header__searchbar-result {
    position: absolute;
    background: white;
    top: 130%;
    left: 0;
    width: 100%;
    border: 1px solid $darkGrey;
    border-radius: 16px;
    padding: 14px 6px 14px 16px;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    color: $darkGrey;
    opacity: 0;
    transform: scale(.8);
    pointer-events: none;
    transition: all .25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    @media (max-width: 620px) {
      font-weight: 300;
      font-size: 10px;
      line-height: 24px;
      width: 250px;
    }
    .header__searchbar-scroll {
      max-height: 300px;
      overflow-y: auto;
      padding-right: 10px;
    }
    .main-page__search-result {
      cursor: pointer;
      .main-page__search-link {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0 5px;
        margin-bottom: 5px;
        border-bottom: 1px solid transparent;
        transition: all .25s ease;
        @media (max-width: 620px) {
          padding: 5px 0 2px;
          margin-bottom: 3px;
        }
        .main-page__company-image {
          display: none;
        }
      }
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          .main-page__search-link {
            border-bottom: 1px solid $lightGreen;
            .main-page__company-text {
              color: $lightGreen;
            }
          }
        }
      }
    }
    &.active {
      opacity: 1;
      transform: scale(1);
      pointer-events: auto;
    }
  }
}