.articles-pages-content{
    display: flex;
    @media (max-width: 825px) {
        flex-direction: column;
    }
    .main-content{
        flex: 10;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            top: 0px;
            right: 0px;
            height: calc(100% + 200px);
            width: 1px;
            background: rgba(101, 97, 123, 1);
        }
        @media (max-width: 825px) {
            &::before{
                display: none;
            }
        }
    }
    .other-content{
        flex: 4;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        @media (max-width: 825px) {
            margin-top: 60px;
        }
    }
}

.articles_page{
    h2{
        width: max-content;
        padding: 15px 90px;
        padding-top: 0;
        margin: 0 auto;
        margin-bottom: 30px;
        border-bottom: 1px solid rgba(101, 97, 123, 1);
        font-size: 35px;
        @media (max-width: 825px) {
            font-size: 24px;
        }
        @media (max-width: 625px) {
            padding: 15px;
            padding-top: 0;
        }
    }
    .content{
        &.faq_content{
            h3{
                font-size: 22px;
                border-bottom: 0;
                margin-bottom: 15px;
                color: #65617B;
                line-height: 1.3;
            }
            p{
                font-size: 14px;
                line-height: 24px;
                margin-bottom: 20px;
                color: #65617B;
            }
        } 
        .articles{
            .active-author{
                width: 100%;
                text-align: center;
                margin-bottom: 30px;
                span{
                    font-size: 24px;
                }
                display: flex;
                align-items: center;
                flex-direction: column;
                a{
                    margin-top: 10px;
                }
            }
            .list{
                width: 1410px;
                @media (max-width: 2000px) {
                    width: 1128px;
                }
                @media (max-width: 1625px) {
                    width: 846px;
                }
                @media (max-width: 1225px) {
                    width: 564px;
                }
                @media (max-width: 625px) {
                    width: 252px;
                }
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                flex-wrap: wrap;
            }
            .pagination{
                margin-top: 30px;
                font-size: 12px;
                button{
                    padding: 5px 20px;
                    outline: 1px solid #0000001f;
                    border: none;
                    margin: 0;
                    color: #65617b96;
                    background: rgba(255, 255, 255, 0);
                    cursor: pointer;
                    &:first-child{
                        border-top-left-radius: 16px;
                        border-bottom-left-radius: 16px;
                    }
                    &:last-child{
                        border-top-right-radius: 16px;
                        border-bottom-right-radius: 16px;
                    }
                    &.active{
                        color: #65617B;
                        background: #e9ebed;
                        pointer-events: none;
                    }
                    &:hover{
                        color: #7d798f;
                        background: #eff1f3cc;
                    }
                }
            }
        }
        .categories{
            .list{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-direction: column;
                border: 1px solid #65617b;
                width: max-content;
                height: max-content;
                .category{
                    width: 100%;
                    text-align: center;
                    padding: 15px 50px;
                    font-size: 16px;
                    color: #65617B;
                    cursor: pointer;
                    user-select: none;
                    &:first-child{
                        padding-top: 30px;
                    }
                    &:last-child{
                        padding-bottom: 30px;
                    }
                    &:hover{
                        color: #9793ad;
                    }
                    &:active{
                        color: #aeaac4;
                    }
                }
            }
        }
    }
}