.account__content {
  @include default-width;
  display: flex;
  position: relative;
}

.sidebar__sticky {
  min-height: 20px;
  @media (max-width: 1000px) {
    min-height: auto;
  }
}

.my-sidebar {
  position: sticky;
  top: 150px;
  left: 0;
  width: 300px;
  min-width: 300px;
  background: white;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.11), 0 1px 10px rgba(0, 0, 0, 0.09), 0 2px 4px rgba(0, 0, 0, 0.16);
  border-radius: 8px;
  height: 800px;
  padding: 8px;
  flex-shrink: revert;
  .sidebar__photo {
    border-radius: 8px 8px 0 0;
  }
  .sidebar__name {
    font-weight: 700;
    font-size: 13px;
    line-height: 18px;
    padding-top: 48px;
    padding-left: 16px;
  }
  .sidebar__tabs {
    padding-top: 32px;
    li {
      display: flex;
      width: 100%;
      padding: 12px;
      box-sizing: border-box;
      align-items: center;
      border-radius: 0 8px 8px 0;
      border-left: 4px solid transparent;
      cursor: pointer;
      .sidebar__tag-title {
        padding-left: 8px;
        font-size: 14px;
        line-height: 21px;
        color: $shadow;
      }
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: $lowOpacityGreen;
        }
      }
      &.active {
        border-color: $green;
        background: $lowOpacityGreen;
        padding-left: 24px;
        .sidebar__tag-title {
          color: black;
        }
      }
    }
  }
}

@media (max-width: 1000px) {
  .account__content {
    display: block;
  }

  .my-sidebar {
    width: 100%;
    max-width: none;
    background: transparent;
    box-shadow: none;
    border-radius: 0;
    height: auto;
    padding: 0;
    position: static;
    .sidebar__photo {
      display: none;
    }
    .sidebar__name {
      display: none;
    }
    .sidebar__tabs {
      padding-top: 0;
      display: flex;
      align-items: center;
      li {
        width: auto;
      }
    }
  }
}

@media (max-width: 550px) {
  .account__content {
    width: 100%;
    padding: 0 20px;
  }


  .my-sidebar {
    min-width: unset;
    .sidebar__tabs {
      li {
        padding: 5px 5px 5px 0;
        img {
          width: 24px!important;
          height: 24px!important;
        }
        .sidebar__tag-title {
          font-size: 10px;
          padding-left: 3px;
        }
        &.active {
          padding-left: 0;
        }
      }
    }
  }
}
