.z-index-3{
  z-index: 3;
}
.mini-graph-table {
  overflow-x: auto;
  display: block;
  padding: 10px 0;
  width: 100%;
  table {
    width: 100%;
  }
  tr {
    border-bottom: 1px solid $shadow;
    position: relative;
    th, td, >span {
      padding: 4px 22px;
      text-align: center;
      height: 100%;
      white-space: nowrap;
      vertical-align: middle;
      &:first-child {
        position: sticky;
        position: -webkit-sticky;
        left: 0;
        white-space: normal;
        div {
          padding: 0 12px 0 22px;
          margin: 4px 0;
        }
        padding: 0;
      }
    }
  }
  thead {
    tr {
      background: $tableGrey;
      th {
        &:first-child {
          background: $tableGrey;
          .mini-graph__first-cell {
            display: flex;
            align-items: center;
            width: 237px;
            justify-content: space-between;
            margin-left: 5px;
          }

        }
        .mini-graph__number-size {
          font-size: 12px;
          color: $lightGrey;
        }
        .mini-graph-table__btns {
          @include btns-box;
          justify-content: center;
          padding: 0;
          >div {
            font-weight: 400;
            padding: 2px 10px;
          }
        }
        font-weight: 700;
        font-size: 12px;
        line-height: 14px;
        color: $darkGrey;
      }
    }
  }
  tbody {
    tr {
      td {
        &:first-child {
          width: 240px;
          background: white;
          div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: white;
            text-align: left;
          }
        }
        font-size: 11px;
        line-height: 14px;
        color: $darkGrey;
        p, a {
          display: block;
          padding-left: 5px;
          overflow: hidden;
          max-width: 100px;
          color: $darkGrey;
        }
        canvas {
          display: block;
          width: 83px;
          height: 27px;
          background: $green;
        }
      }
      &:nth-child(even) {
        background: $tableGrey;
        td {
          &:first-child {
            background: $tableGrey;
            div {
              background: $tableGrey;
            }
          }
          canvas {
            background: $red;
          }
        }
      }
    }
  }
}

@media (max-width: 1000px) {
  .mini-graph-table {
    padding-bottom: 10px;
    tr {
      th, td {
        padding: 8px 12px;
        &:first-child {
          position: sticky;
          left: 0;
          background: white;
          div {
            background: white;
            padding: 0;
          }
        }
      }
    }
    thead {
      tr {
        background: transparent;
        th:first-child {
          background: none;
          flex-direction: column;
          justify-content: center;
          margin-left: 0;
          .mini-graph__first-cell {
            width: auto;
            flex-direction: column;
            justify-content: center;
            margin-left: 0;
          }
          .mini-graph__number-size {
            margin-left: 0;
            margin-right: 0;
            order: 2;
          }
          .mini-graph-table__btns {
            margin-left: 0;
            >div {

              padding: 2px 9px;
            }
          }
        }

      }
    }
    tbody {
      tr {
        td {
          &:first-child {
            width: auto;
            background: white;
            div {
              background: white;
              display: block;
              padding-left: 5px;
              padding-right: 10px;
              p {
                padding-left: 0;
                padding-bottom: 10px;
              }
            }
          }
          canvas {
            width: 48px;
            height: 16px;
            margin-right: 20px;
          }
        }
        &:nth-child(even) {
          background: transparent;
          td {
            &:first-child {
              background: white;
              div {
                background: white;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 425px) {
  .mini-graph-table__btns {
    .currency-trigger{
      min-width: 50px;
    }
  }
}