.notAuth{
  position: relative;
  .message{
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 4;
    padding: 16px 40px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 5px rgba(0, 0, 0, 0.11), 0px 1px 10px rgba(0, 0, 0, 0.09), 0px 2px 4px rgba(0, 0, 0, 0.16);
    border-radius: 4px;
    span{
      color: #65617B;
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
    }
    transition: 0.3s;
    &:hover{
      background: rgb(236, 236, 236);
    }
    &:active{
      background: rgb(221, 220, 220);
    }
  }
}

td{
  position: relative;
  .blury{
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    &::before{
      content: '4826';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: transparent;
      text-shadow: 0 0 5px #65617B;
    }
    &.gray{
      background: #ededed;
    }
  }
}

.two-items-table {
  padding-bottom: 20px;
  .two-items-table__row {
    display: flex;
    justify-content: space-around;
    padding: 8px 0;
    box-shadow: 0 0 1px $shadow;
    border-bottom: 1px solid $shadow;
    &:nth-child(odd) {
      background: $tableGrey;
    }
    .two-items-table__item {
      font-size: 11px;
      line-height: 16px;
      width: 50%;
      text-align: center;
      .two-items-table__number {
        padding-top: 4px;
      }
    }
  }
}


@media (max-width: 700px) {
  .two-items-table {
    padding: 11px 30px;
    margin-top: 13px;
    border: 1px solid $tableGrey;
    border-radius: 8px;
    .two-items-table__row {
      justify-content: space-around;
      padding: 8px 0;
      box-shadow: none;
      border: none;
      &:nth-child(odd) {
        background: none;
      }
    }
  }
}

@media (max-width: 500px) {
  .two-items-table {
    .two-items-table__row {
      justify-content: space-between;
    }
  }
}