.financials-graph {
  padding-top: 17px;
  .financials-graph__content {
    @include default-width;
    filter: drop-shadow(0 4px 5px rgba(0, 0, 0, 0.11)) drop-shadow(0 1px 10px rgba(0, 0, 0, 0.09)) drop-shadow(0 2px 4px rgba(0, 0, 0, 0.16));
    border-radius: 4px;
    background: white;
    position: relative;
    .financials-graph__btns {
      position: relative;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 60px;
      .financials__download {
        position: absolute;
        left: 15px;
        a {
          font-size: 11px;
          line-height: 20px;
          letter-spacing: 1.25px;
          color: white;
          padding: 8px 6px 8px 28px;
          background: $green;
          border-radius: 18px;
          position: relative;
          user-select: none;
          &:before {
            content: "";
            position: absolute;
            width: 12px;
            height: 12px;
            // background: url("/images/financials/download.svg");
            left: 8px;
            top: 50%;
            transform: translate(0 , -50%);
          }
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: $lightGreen;
            }
          }
          &:focus {
            background: $veryLightGreen;
          }
        }
      }
      .financials__middle-btns {
        @include btns-box;
        padding: 0;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }
      .financials__right-btns {
        @include btns-box;
        padding: 0;
        position: absolute;
        right: 27px;
      }
    }
    canvas {
      height: 350px;
      width: 100%;
    }
  }
}


.financials-tables {
  padding-top: 43px;
  @include default-width;
  .financials-tables__navigation {

    display: flex;
    align-items: center;
    justify-content: space-between;
    .financials-tables__first-table-name {
      font-size: 20px;
      line-height: 18px;
      color: $darkGrey;
      width: 220px;
    }
    .financials-tables__middle-btns {
        @include btns-box;
        margin-left: 0;
        padding: 0;
    }
    .financials-tables__right-nav {
      display: flex;
      .financials-tables__years-btns {
        @include btns-box;
        margin-right: 5px;
        padding: 0;
      }
      .settings {
        padding: 0;
      }
    }
  }
  .interactive-table {
    margin-bottom: 56px;
    .interactive-table__title {
      font-size: 20px;
      line-height: 18px;
      color: $darkGrey;
      position: sticky;
      margin-bottom: 13px;
      left: 0;
    }
    .interactive {
      margin-top: 22px;
      tbody {
        tr {
          cursor: pointer;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: $lowOpacityGreen;
              td:first-child {
                background: $lowOpacityGreen;
                div {
                  background: $lowOpacityGreen;
                }
              }
            }
          }
          //&:focus {
          //  background: $highOpacityGreen;
          //  td:first-child {
          //    div {
          //      background: $highOpacityGreen;
          //    }
          //  }
          //}
          &.active {
            background: $lowOpacityGreen;
            td:first-child {
              background: $lowOpacityGreen;
              div {
                background: $lowOpacityGreen;
              }
            }
          }
          div {
            position: relative;
            p, a {
              cursor: pointer;
              &:hover {
                text-decoration: underline!important;
              }
              &:focus {
                text-decoration: underline!important;
              }
            }
            .interactive__circle {
              position: absolute;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              left: 10px;
              top: 50%;
              transform: translateY(-50%);
              border: 1px solid $darkGrey;
              transition: all .25s ease;
            }
            &.active {
              .interactive__circle {
                background: $lightGreen;
              }
            }
          }
          &.active-row {
            background: $lowOpacityGreen;
            td:first-child {
              background: $lowOpacityGreen;
              div {
                background: $lowOpacityGreen;
              }
            }
          }
        }
      }
    }
  }
}

.history {
  .history__title {
    font-size: 20px;
    line-height: 18px;
    color: $darkGrey;
  }
  table {
    width: 100%;
    max-width: 350px;
    margin-top: 13px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    tr {
      border-bottom: 1px solid $tableGrey;
      th, td {
        &:first-child {
          border-right: 1px solid $tableGrey;
          padding: 7px 16px;
          font-weight: 700;
        }
        text-align: center;
        color: $darkGrey;
        padding: 7px;
      }
    }
    thead {
      tr {
        background: $tableGrey;
        th {
          font-weight: 700;
          font-size: 12px;
          line-height: 22px;
          background: $tableGrey;
        }
      }
    }
    tbody {
      tr {
        td {
          font-size: 11px;
          line-height: 22px;
          a {
            color: $darkGrey;
            text-decoration: underline!important;
            @include footer-head;
          }
        }
      }
    }
  }
}


@media (max-width: 1000px) {
  .financials {
    .financials-tables {
      .interactive-table {
        .interactive {
          tbody {
            tr {
              div {
                position: relative;
                padding-left: 30px;
                .interactive__circle {
                  left: 8px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .financials-graph {
    padding: 110px 20px 0;
    .financials-graph__content {
      width: 100%;
      .financials-graph__btns {
        position: absolute;
        padding: 0;
        top: -40px;
        left: 0;
        right: 0;
        height: 26px;
        .financials__download {
          left: 0;
          a {
            font-size: 9px;
            padding: 6px 6px 6px 28px;
          }
        }
        .financials__middle-btns {
          padding: 0;
          top: -50px;
        }
        .financials__right-btns {
          padding: 0;
          right: 0;
        }
      }
      canvas {
        height: 180px;
      }
    }
  }

  .financials-tables {
    padding-top: 12px;
    .financials-tables__navigation {
      flex-wrap: wrap;
      justify-content: center;
      .financials-tables__first-table-name {
        font-size: 14px;
        line-height: 18px;
        display: none;
      }
      .financials-tables__middle-btns {
        margin-left: 0;
        padding: 0!important;
      }
      .financials-tables__right-nav {
        width: 100%;
        display: flex;
        padding-top: 20px;
        justify-content: space-between;
        .financials-tables__years-btns {
          padding: 0!important;
        }
        .settings {
          padding: 0!important;
        }
      }
    }
  }

}

@media (max-width: 340px) {
  .financials-graph {
    .financials-graph__content {
      .financials-graph__btns {
        .financials__download {
          a {
            padding: 8px 6px;
            &:before {
              display: none!important;
            }
          }
        }
        .financials__middle-btns {
          padding: 0;
        }
        .financials__right-btns {
          padding: 0;
        }
      }
    }
  }
}