.main-page__hero {
  background: linear-gradient(rgb(228,225,200), rgb(237,182,140));
  margin-top: -100px;
  padding-top: 180px;
  padding-bottom: 100px;
  .hero__content {
    @include default-width;
    display: flex;
    justify-content: space-between;
    .hero__left {
      width: 46%;
      flex-shrink: 1;
      padding-top: 80px;
      .hero__title {
        text-align: center;
        font-weight: 700;
        font-size: 48px;
        line-height: 80px;
        color: $landingBlack;
      }
      .hero__subtitle {
        font-size: 24px;
        line-height: 20px;
        color: black;
        padding-top: 32px;
        text-align: center;
      }
      .hero__search {
        padding-top: 90px;
        position: relative;
        margin-left: 16px;
        .hero__searchbar {
          width: 100%;
          display: block;
          position: relative;
          input {
            font-weight: 300;
            font-size: 14px;
            line-height: 21px;
            padding: 14px 48px 14px 16px;
            border: 1px solid $darkGrey;
            border-radius: 31px;
            width: 100%;
            outline: none;
          }
          &:after {
            content: "";
            position: absolute;
            width: 24px;
            height: 24px;
            // background: url("/images/header/magnifying-glass.svg");
            right: 16px;
            top: 50%;
            transform: translateY(-50%);
          }
        }
        .hero__search-results {
          display: none;
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          background: white;
          border: 1px solid $darkGrey;
          border-radius: 31px;
          width: 100%;
          max-width: 600px;
          z-index: 50;
          padding: 14px 16px;
          margin-top: 10px;
          .hero__search-results-scroll {
            overflow-y: auto;
            max-height: 300px;
            .hero__search-result {
              .hero__search-link {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 10px 0 5px;
                margin-bottom: 5px;
                border-bottom: 1px solid transparent;
                transition: all .25s ease;
                .hero__company-text {
                  font-weight: 300;
                  font-size: 14px;
                  line-height: 21px;
                  color: black;
                  transition: all .25s ease;
                }
                .hero__company-image {
                  display: none;
                }
              }
              @media (hover: hover) and (pointer: fine) {
                &:hover {
                  .hero__search-link {
                    border-bottom: 1px solid $lightGreen;
                    .hero__company-text {
                      color: $lightGreen;
                    }
                  }
                }
              }
            }
          }

          &.show {
            display: block;
          }
        }
      }
    }
    .hero__right {
      width: 40%;
      .hero__image {
        position: relative;
        border-radius: 52px;
        padding-right: 40px;
        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 90%;
          border-radius: 52px;
          border: 2px solid $black11;
          left: -70px;
          top: -60px;
        }
        span, img {
          position: relative;
          z-index: 20;
          width: 100%;
          border-radius: 52px;
        }
        &:after {
          content: "";
          position: absolute;
          width: 60%;
          height: 75%;
          border-radius: 52px;
          border: 2px solid $black11;
          right: 0;
          bottom: -60px;
        }
      }
    }
  }
}

.main-page__analytics {
  position: relative;
  padding-top: 80px;
  .analytics__bg {
    position: absolute;
    right: 0;
    top: 0;
  }
  .analytics__content{
    position: relative;
    z-index: 10;
    @include default-width;
    padding-top: 120px;
    padding-bottom: 180px;
    display: flex;
    justify-content: space-between;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      max-width: 700px;
      height: 1px;
      background: $darkGrey;
      top: 0;
      left: 50%;
      transform: translate(-50% , 0);
    }
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      max-width: 700px;
      height: 1px;
      background: $darkGrey;
      bottom: 0;
      left: 50%;
      transform: translate(-50% , 0);
    }
    .analytics__left {
      width: 40%;
      padding-top: 40px;
      .analytics__photo {
        text-align: center;
        position: relative;
        padding-left: 60px;
        &:before {
          content: "";
          position: absolute;
          width: 90%;
          height: 90%;
          border-radius: 52px;
          border: 2px solid $black11;
          left: 0;
          top: -60px;
        }
        img {
          position: relative;
          z-index: 20;
          width: 100%;
        }
        &:after {
          content: "";
          position: absolute;
          width: 60%;
          height: 75%;
          border-radius: 52px;
          border: 2px solid $black11;
          right: -40px;
          bottom: -60px;
        }
      }
    }
    .analytics__right {
      width: 46%;
      display: flex;
      flex-direction: column;
      .analytics__title {
        font-weight: 700;
        font-size: 48px;
        line-height: 80px;
        color: $landingBlack;
        text-align: center;
      }
      .analytics__subtitle {
        font-size: 24px;
        line-height: 20px;
        color: black;
        padding-top: 32px;
        text-align: center;
      }
      .analytics__list {
        margin: 0 auto;
        padding-top: 146px;
        li {
          font-size: 23px;
          line-height: 70px;
          font-style: italic;
          position: relative;
          padding-left: 25px;
          &:before {
            content: "";
            position: absolute;
            width: 19px;
            height: 24px;
            left: 0;
            top: 50%;
            transform: translate(0 , -50%);
            // background: url("/images/main-page/tick.svg");
          }
        }
      }
    }
  }
}


.main-page__data {
  position: relative;
  padding-top: 88px;
  .data__bg {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .data__content {
    @include default-width;
    position: relative;
    display: flex;
    justify-content: space-between;
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      max-width: 700px;
      height: 1px;
      background: $darkGrey;
      bottom: 0;
      left: 50%;
      transform: translate(-50% , 0);
    }
    .data__left {
      width: 100%;
      max-width: 466px;
      text-align: center;
      padding-top: 130px;

      .data__title {
        font-weight: 700;
        font-size: 48px;
        line-height: 80px;
      }
      .data__subtitle {
        font-size: 24px;
        line-height: 20px;
        padding-top: 32px;
      }
    }
    .data__right {
      width: 55%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .data__card {
        width: 48%;
        display: flex;
        align-items: center;
        margin-bottom: 120px;
        .data__text-box {
          max-width: 210px;
          padding-left: 32px;
          box-sizing: content-box;
          .data__card-title {
            font-size: 28px;
            line-height: 32px;
            color: $black33;
          }
          .data__card-text {
            padding-top: 4px;
            font-size: 15px;
            line-height: 17px;
            color: $black33;
          }
        }
      }
    }
  }
}

.main-page__subscriptions {
  padding-top: 80px;
  .subscriptions__content {
    @include default-width;
    max-width: 750px;
    .subscriptions__title {
      font-weight: 700;
      font-size: 48px;
      line-height: 55px;
      max-width: 680px;
      margin: 0 auto;
      text-align: center;
      @media (max-width: 800px) {
        font-size: 40px;
        line-height: 40px;
      }
    }
    .subscriptions__cards {
      padding-top: 81px;
      display: flex;
      justify-content: space-between;
      @media (max-width: 800px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      .subscriptions__card {
        padding: 36px 10px 88px;
        background: #FFFFFF;
        border: 1px solid #000000;
        border-radius: 20px;
        max-width: 300px;
        position: relative;
        text-align: center;
        margin-bottom: 40px;
        .subscriptions__img {
          text-align: center;
        }
        .subscriptions__name {
          font-size: 20px;
          line-height: 23px;
          padding-top: 38px;
          text-align: center;
          padding-bottom: 23px;
          position: relative;
          .subscriptions__bonus {
            font-weight: 700;
            color: $lightGreen;
            font-size: 16px;
            line-height: 18px;
            position: absolute;
            width: 100%;
            left: 50%;
            bottom: 0;
            transform: translate(-50%, 0);
          }
        }
        .subscriptions__price {
          font-size: 48px;
          line-height: 55px;
          color: $lightGreen;
          text-align: center;
          position: relative;
          display: inline-block;
          margin: 0 auto;
          &.sale {
            &:after {
              content: "";
              position: absolute;
              width: 110%;
              height: 2px;
              background: $lightGreen;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
          }
        }
        .subscriptions__pluses {
          display: flex;
          flex-wrap: wrap;
          padding-top: 35px;
          li {
            font-weight: 300;
            font-size: 12px;
            line-height: 18px;
            padding: 1px 12px;
            background: rgba(58, 182, 125, 0.05);
            border: 1px solid $green;
            border-radius: 16px;
            margin-right: 4px;
            margin-bottom: 10px;
          }
        }
        .subscriptions__text {
          font-weight: 300;
          font-size: 13px;
          line-height: 21px;
          padding-top: 30px;
          text-align: left;
        }
        .subscriptions__btn-box {
          text-align: center;
          padding-top: 35px;
          position: absolute;
          left: 0;
          width: 100%;
          bottom: 18px;
          .subscriptions__btn {
            color: white;
            font-weight: 700;
            font-size: 14px;
            line-height: 20px;
            padding: 8px 18px;
            background: $green;
            border-radius: 18px;
            text-align: center;
            min-width: 180px;
            display: inline-block;
          }
        }
      }
    }
  }
}

@media (max-width: 1275px) {
  .main-page__data {
    .data__content {
      @include default-width;
      display: block;
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        max-width: 700px;
        height: 1px;
        background: $darkGrey;
        bottom: 0;
        left: 50%;
        transform: translate(-50% , 0);
      }
      .data__left {
        width: 100%;
        max-width: none;
        text-align: center;
        padding-top: 50px;
      }
      .data__right {
        width: 100%;
        padding-top: 40px;
        .data__card {
          .data__text-box {
            max-width: none;
          }
        }
      }
    }
  }
}



@media (max-width: 1100px) {
  .main-page__hero {
    .hero__content {
      .hero__left {
        .hero__title {
          font-size: 42px;
        }
        .hero__subtitle {
          font-size: 16px;
          padding-top: 18px;
        }
        .hero__search {
          padding-top: 40px;
          margin-left: 0;
          .hero__search-results {
            .hero__search-results-scroll {
              padding-right: 10px;
              max-height: 250px;
            }
          }
        }
      }
    }
  }

  .main-page__analytics {
    .analytics__bg {
      img {
        width: 100%;
        height: 100%;
        max-height: 800px;
      }
    }
    .analytics__content{
      .analytics__left {
        width: 48%;
      }
      .analytics__right {
        width: 46%;
        display: flex;
        flex-direction: column;
        .analytics__title {
          font-size: 36px;
        }
        .analytics__subtitle {
          font-size: 24px;
          padding-top: 16px;
        }
        .analytics__list {
          margin: 0 auto;
          padding-top: 80px;
        }
      }
    }
  }
}

@media (max-width: 850px) {
  .main-page__hero {
    margin-top: -100px;
    padding-top: 130px;
    padding-bottom: 65px;
    .hero__content {
      display: block;
      .hero__left {
        width: 100%;
        padding-top: 0;
        .hero__title {
          font-size: 40px;
        }
        .hero__subtitle {
          font-size: 18px;
          line-height: 23px;
          padding-top: 14px;
        }
        .hero__search {
          padding-top: 26px;
          .hero__search-results {
            .hero__search-results-scroll {
              max-height: 200px;
              .hero__search-result {
                .hero__search-link {
                  .hero__company-text {
                    &:first-child {
                      padding-right: 20px;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .hero__right {
        width: 100%;
        margin: 0 auto;
        padding-top: 70px;
        text-align: center;
        .hero__image {
          border-radius: 24px;
          padding-right: 0;
          &:before {
            width: 70%;
            height: 90%;
            left: 0;
            top: -30px;
            border-radius: 24px;
          }
          img {
            width: 70%;
            border-radius: 24px;
          }
          &:after {
            width: 60%;
            height: 75%;
            right: 0;
            bottom: -30px;
            border-radius: 24px;
          }
        }
      }
    }
  }

  .main-page__analytics {
    position: relative;
    padding-top: 80px;
    .analytics__bg {
      position: absolute;
      right: 0;
      top: 0;
      img {
        width: 100%;
        height: 100%;
        max-height: 600px;
      }
    }
    .analytics__content{
      position: relative;
      z-index: 10;
      @include default-width;
      padding-top: 48px;
      padding-bottom: 90px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;

      &:before {
        content: "";
        position: absolute;
        width: 100%;
        max-width: 700px;
        height: 1px;
        background: $darkGrey;
        top: 0;
        left: 50%;
        transform: translate(-50% , 0);
      }
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        max-width: 700px;
        height: 1px;
        background: $darkGrey;
        bottom: 0;
        left: 50%;
        transform: translate(-50% , 0);
      }
      .analytics__left {
        width: 100%;
        padding-top: 80px;
        order: 2;
        .analytics__photo {
          text-align: center;
          position: relative;
          padding-left: 0;
          &:before {
            border-radius: 24px;
            width: 70%;
          }
          img {
            position: relative;
            z-index: 20;
            width: 70%;
          }
          &:after {
            border-radius: 24px;
            right: 0;
          }
        }
      }
      .analytics__right {
        width: 100%;
        display: flex;
        flex-direction: column;
        order: 1;
        .analytics__title {
          font-size: 40px;
          line-height: 40px;
        }
        .analytics__subtitle {
          font-size: 20px;
          line-height: 20px;
          padding-top: 24px;
        }
        .analytics__list {
          margin: 0 auto;
          padding-top: 24px;
          li {
            font-size: 18px;
            line-height: 70px;
          }
        }
      }
    }
  }

  .main-page__data {
    position: relative;
    padding-top: 44px;
    .data__bg {
      img {
        max-height: 400px;
      }
    }
    .data__content {
      .data__left {
        .data__title {
          font-size: 40px;
          line-height: 40px;
        }
        .data__subtitle {
          font-size: 18px;
          line-height: 20px;
          padding-top: 32px;
        }
      }
      .data__right {
        justify-content: center;
        .data__card {
          width: 100%;
          max-width: 300px;
          margin-bottom: 32px;
          .data__text-box {
            padding-left: 20px;
            .data__card-title {
              font-size: 20px;
              line-height: 23px;
            }
            .data__card-text {
              font-size: 14px;
              line-height: 16px;
            }
          }
        }
      }
    }
  }
}