
header {
  background: white;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.11), 0 1px 10px rgba(0, 0, 0, 0.09), 0 2px 4px rgba(0, 0, 0, 0.16);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2000;
  user-select: none;
  .header__content {
    box-sizing: border-box;
    @include default-width;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    .header__left {
      display: flex;
      align-items: center;
      .header__logo {
        font-weight: 700;
        font-size: 35px;
        line-height: 18px;
        color: black;
        display: block;
        user-select: none;
      }
      .header__searchbar {
        input {
          border: 1px solid $darkGrey;
          border-radius: 16px;
          background: white;
          font-family: 'Roboto', sans-serif;
          color: $darkGrey;
          padding: 8px 35px 8px 52px;
          width: 100%;
          box-sizing: border-box;
          outline: none;
          &::-webkit-search-cancel-button {
            position:relative;
            cursor: pointer;
            -webkit-appearance: none;
            height: 14px;
            width: 14px;
            // background: url("/images/header/cross.svg");
          }
        }

        &:before {
          content: "";
          position: absolute;
          width: 24px;
          height: 24px;
          // background: url("/images/header/magnifying-glass.svg");
          top: 50%;
          left: 16px;
          transform: translateY(-50%);
        }
        .header__search-cross {
          position: absolute;
          top: 8px;
          right: 12px;
        }
      }
    }
    .header__right {
      display: flex;
      .header__circles {
        display: flex;
        align-items: center;
        .header__question-image, .header__language, .header__profile  {
          position: relative;
          cursor: pointer;
          margin-right: 20px;
          width: 28px;
          height: 28px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-content: center;
          align-items: center;
          @include settings-head;
          &:last-child {
            margin-right: 0;
          }
        }
        .question__tooltip {
          min-height: 240px;
          border: 1px solid $tabsGrey;
          padding: 20px 24px;
          box-sizing: border-box;
          background: white;
          visibility: hidden;
          pointer-events: none;
          &.active {
            visibility: visible;
            pointer-events: unset;
          }
        }
      }
      .header__btns {
        display: flex;
        margin-left: 80px;
        user-select: none;
        a {
          border-radius: 18px;
          font-weight: 700;
          font-size: 12px;
          line-height: 18px;
          padding: 6px 12px;
          margin-left: 10px;
          cursor: pointer;
          &:first-child {
            margin-left: 0;
          }
        }
        .header__login {
          color: $darkGrey;
          border: 1px solid $darkGrey;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: $tableGrey;
            }
          }
          &:focus {
            background: $tabsGrey;
          }
          &:active {

          }
        }
        .header__registration {
          background: linear-gradient(180deg, $green 0%, #22AB94 100%);
          border: 1px solid transparent;
          color: white;
          @media (hover: hover) and (pointer: fine) {
            &:hover {
              background: linear-gradient(180deg, #22AB94 0%, $green 100%);
            }
          }
          &:focus {
            background: linear-gradient(180deg, #22AB94 0%, $green 100%);
          }
        }
      }

    }
  }
}

.pop-up {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.24);
  backdrop-filter: blur(12px);
  top: 0;
  left: 0;
  z-index: 100;
  .pop-up__content {
    padding: 25px 92px;
    background: white;
    filter: drop-shadow(0 4px 5px rgba(0, 0, 0, 0.11)) drop-shadow(0 1px 10px rgba(0, 0, 0, 0.09)) drop-shadow(0 2px 4px rgba(0, 0, 0, 0.16));
    text-align: center;
    color: black;
    position: absolute;
    max-width: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .pop-up__title {
      font-weight: 700;
      font-size: 20px;
      line-height: 18px;
    }
    .pop-up__google {
      padding: 6px 20px 6px 9px;
      font-weight: 700;
      font-size: 12px;
      line-height: 20px;
      color: rgba(0, 0, 0, 0.54);
      border: 1px solid $blue;
      border-radius: 7px;
      display: inline-flex;
      align-items: center;
      margin-top: 70px;
      span {
        padding-left: 8px;
      }
    }
    .pop-up__other {
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      padding-top: 80px;
      a {
        color: $green;
        padding-left: 2px;
      }
    }
    .pop-up__terms {
      font-weight: 400;
      font-size: 11px;
      line-height: 20px;
      max-width: 416px;
      margin: 0 auto;
      padding-top: 68px;
    }
  }
}


@media (max-width: 750px) {
  header {
    .header__content {
      padding: 18px 16px;
      width: 100%;
      .header__left {
        .header__logo {
          font-size: 13px;
          line-height: 18px;
        }
        .header__searchbar {
          input {
            font-size: 10px;
            line-height: 24px;
            padding: 2px 5px 2px 26px;
            &::-webkit-search-cancel-button {
              height: 13px;
              width: 13px;
              // background: url("/images/header/cross-small.svg");
            }
          }
          &:before {
            width: 17px;
            height: 17px;
            // background: url("/images/header/magnifying-glass-small.svg");
            top: 50%;
            left: 7px;
            transform: translateY(-50%);
          }
        }
      }
      .header__right {
        display: flex;
        max-width: none;
        width: auto;
        justify-content: unset;
        .header__circles {
          >div {
            margin-right: 5px;
          }
        }
        .header__btns {
          display: flex;
          margin-left: 10px;
          a {
            border-radius: 18px;
            font-weight: 700;
            font-size: 12px;
            line-height: 18px;
            padding: 6px 12px;
            margin-left: 10px;
            &:first-child {
              margin-left: 0;
            }
          }
          .header__login {
            color: $darkGrey;
            border: 1px solid $darkGrey;

          }
          .header__registration {
            background: linear-gradient(180deg, $green 0%, #22AB94 100%);
            border: 1px solid transparent;
            color: white;
          }
        }
 
      }
    }
  }
  .pop-up{
    .pop-up__content{
      max-width: 95vw;
      padding: 25px;
    }
  }
}

@media (max-width: 450px) {
  .pop-up{
    .pop-up__content{
      .pop-up__title {
        font-size: 18px;
        line-height: 16px;
      }
      .pop-up__google {
        padding: 4px 15px 4px 7px;
        font-size: 10px;
        line-height: 18px;
        margin-top: 50px;
        span {
          padding-left: 6px;
        }
      }
      .pop-up__other {
        font-size: 10px;
        line-height: 18px;
        padding-top: 60px;
      }
      .pop-up__terms {
        font-size: 9px;
        line-height: 18px;
        max-width: 300px;
        padding-top: 48px;
      }
    }
  }
}