.company {
  padding-top: 85px;
  .company__content {
    @include default-width;
    padding-bottom: 8px;
    border-bottom: 1px solid $lightGrey;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    text-transform: uppercase;
    .company__left {
      display: flex;
      justify-content: space-between;
      width: 50%;
      .company__first {
        display: flex;
        align-items: baseline;
        .company__box {
          width: 24px;
          height: 24px;
          background: $box;
        }
        .company__main-info {
          padding-left: 8px;
          .company__name {
            font-weight: 400;
            font-size: 30px;
            line-height: 40px;
            color: black;
          }
          .company__main-bottom {
            display: flex;
            justify-content: space-between;
            .company__main-secondary-info {
              font-weight: 400;
              font-size: 12px;
              line-height: 24px;
              color: $lightGrey;
            }
          }
        }
      }
      .company__second {
        .company__price {
          display: flex;
          align-items: center;
          .company__main-price {
            display: flex;
            align-items: baseline;
            p {
              font-weight: 400;
              font-size: 30px;
              line-height: 40px;
              color: black;
              padding-right: 2px;
            }
            span {
              font-weight: 400;
              font-size: 12px;
              line-height: 16px;
              color: $lightGrey;
            }
          }
          .company__percents {
            display: flex;
            padding-left: 7px;
            p {
              font-weight: 400;
              font-size: 16px;
              line-height: 28px;
              color: $lightGreen;
            }
          }
        }
        .company__market-status {
          font-weight: 400;
          font-size: 12px;
          line-height: 14px;
          text-transform: uppercase;
          padding-top: 3px;
        }
      }
    }
    .company__right {
      ul {
        display: flex;
        li {
          padding-right: 36px;
          p {
            font-weight: 400;
            font-size: 17px;
            line-height: 28px;
            color: black;
          }
          h3 {
            font-weight: 400;
            font-size: 11px;
            line-height: 16px;
            color: $lightGrey;
            text-transform: uppercase;
          }
          &:last-child {
            padding-right: 3px;
          }
        }
      }
    }
  }
}


@media (max-width: 1000px) {
  .company {
    padding-top: 85px;
    .company__content {
      display: block;
      width: 100%;
      .company__left {
        width: 100%;

        border-bottom: 1px solid $lightGrey;
        padding: 0 20px 8px;
      }
      .company__right {
        padding: 8px 20px 0;
        ul {
          justify-content: space-between;
        }
      }
    }
  }
}

@media (max-width: 500px) {
  .company {
    padding-top: 16px;
    .company__content {
      padding-bottom: 22px;
      .company__left {
        padding-bottom: 22px;
        align-items: flex-start;
        .company__first {
          align-items: flex-end;
          .company__main-info {
            padding-left: 4px;
            .company__name {
              font-size: 20px;
              line-height: normal;
            }
            .company__main-bottom {
              .company__main-secondary-info {
                font-size: 10px;
                line-height: normal;
              }
            }
          }
        }
        .company__second {
          .company__price {
            display: block;
            .company__main-price {
              align-items: flex-start;
              p {
                font-size: 18px;
                line-height: normal;
              }
              span {
                font-size: 10px;
                line-height: normal;
              }
            }
            .company__percents {
              display: flex;
              padding-left: 7px;
              p {
                font-size: 10px;
                line-height: normal;
              }
            }
          }
          .company__market-status {
            font-size: 8px;
            line-height: normal;
          }
        }
      }
      .company__right {
        padding-top: 22px;
        ul {
          display: flex;
          li {
            padding-right: 0;
            p {
              font-size: 11px;
              line-height: normal;
            }
            h3 {
              font-size: 11px;
              line-height: normal;
            }
            &:last-child {
              padding-right: 0;
            }
          }
        }
      }
    }
  }
}