.breadcrumbs {
  ul {
    @include default-width;
    display: flex;
    li {
      margin-right: 10px;
      display: flex;
      a {
        color: $darkGrey;
        font-weight: 300;
        font-size: 13px;
        line-height: 18px;
        position: relative;
        padding-right: 10px;
        display: block;
        user-select: none;
        @include footer-head;
      }
      &:after {
        content: "/";
        font-weight: 300;
        font-size: 14px;
        line-height: 21px;
        color: $darkGrey;
      }
      &:last-child {
        margin-right: 0;
        &:after {
          display: none;
        }
        a {
          pointer-events: none;
        }
      }
    }
  }
}